<template>
  <div>
    <el-row v-for="rn in rowNum" v-bind:key="rn">
      <el-col :span="8" v-for="i in 3" v-bind:key="i">
        <el-card class="box-card" shadow="hover" @click.native="clickCard">
          <div slot="header" class="clearfix">
            <span> {{ (((rn-1) * 3) + i) }}</span>
            <el-button style="float: right; padding: 3px 0" type="text"></el-button>
          </div>

          <el-col :span="18">
            <div v-for="n in 3" :key="n" class="text item">
              {{' ' + n }}
            </div>
          </el-col>
          <el-col :span="6">
            <el-checkbox v-model="checked" v-if="isDeleting"></el-checkbox>
          </el-col>
        </el-card>
      </el-col>
    </el-row>
    <!--  -->
    <el-row v-if="lastRow > 0">
      <el-col :span="8" v-for="i in lastRow" v-bind:key="i">
        <el-card class="box-card" shadow="hover" @click.native="clickCard">
          <div slot="header" class="clearfix">
            <span>{{ intvweeNum - lastRow + i }}</span>
            <el-button style="float: right; padding: 3px 0" type="text"></el-button>
          </div>
          <el-col :span="18">
            <div v-for="n in 3" :key="n" class="text item">
              {{' ' + n }}
            </div>
          </el-col>
          <el-col :span="6">
            <el-checkbox v-model="checked" v-if="isDeleting"></el-checkbox>
          </el-col>
        </el-card>
      </el-col>
    </el-row>

    <!--  delete  -->
    <el-button icon="el-icon-close" plain class="cancel-delete" v-if="isDeleting" @click="clickCancelDelete"></el-button>
    <el-button icon="el-icon-delete" type="danger" plain class="confirm-delete" v-if="isDeleting" @click="clickConfirmDelete"></el-button>

    <!--  -->
    <el-dialog title="" :visible.sync="intvwDialogFormVisible">
      <el-form :model="form">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="email" :label-width="formLabelWidth">
          <el-input v-model="form.email" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form.region" placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="intvwDialogFormVisible = false"></el-button>
        <el-button type="primary" @click="intvwDialogFormVisible = false"></el-button>
      </div>
    </el-dialog>

    <!--  HR  -->
    <el-dialog width=85% :visible.sync="hrDialogFormVisible">
      <el-row :gutter="20">
        <el-col :span="7">
          <h3>HR </h3>
          <el-form :model="form">
            <el-form-item label="" :label-width="hrFormLabelWidth">
              <span v-if="editHrInfo === false" > {{ form.name }} </span>
              <el-input v-else v-model="form.name" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="email" :label-width="hrFormLabelWidth">
              <span v-if="editHrInfo === false" > {{ form.email }} </span>
              <el-input v-else v-model="form.email" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <div class="hrinfo-footer">
            <el-button size="medium" type="primary" plain v-if="editHrInfo === false" @click="editHrInfo = true"> </el-button>
            <el-button size="medium" v-if="editHrInfo" @click="editHrInfo = false"> </el-button>
            <el-button size="medium" type="primary" plain v-if="editHrInfo" @click="editHrInfo = false"> </el-button>
          </div>
        </el-col>
        <el-col :span="1">
          <el-divider direction="vertical"></el-divider>
        </el-col>
        <el-col :span="8">
          <h3> HR </h3>
          <el-table
            ref="multipleTable"
            :data="intvweeTableData"
            style="width: 100%"
            max-height="340">
            <el-table-column type="expand">
              <template slot-scope="props">
                <el-form label-position="left" inline class="demo-table-expand">
                  <el-form-item label="">
                    <span>{{ props.row.name }}</span>
                  </el-form-item>
                  <el-form-item label=" ID">
                    <span>{{ props.row.id }}</span>
                  </el-form-item>
                  <el-form-item label="email">
                    <span>{{ props.row.email }}</span>
                  </el-form-item>
                  <el-form-item label="">
                    <span>{{ props.row.category }}</span>
                  </el-form-item>
                </el-form>
              </template>
            </el-table-column>
            <el-table-column
              label=""
              prop="name"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              label="email"
              prop="email">
            </el-table-column>
            <el-table-column
              type="selection"
              width="55">
            </el-table-column>
          </el-table>
          <div class="hr-intvwee-footer">
            <el-button size="medium" type="danger" plain @click="hrDialogFormVisible = true"> </el-button>
            <el-button size="medium" type="primary" plain @click="addIntvweeDialogFormVisible = true"> </el-button>
          </div>
        </el-col>
        <el-col :span="1">
          <el-divider direction="vertical"></el-divider>
        </el-col>
        <el-col :span="7">
          <h3> HR </h3>
          <el-table
            ref="multipleTable"
            :data="intvwerTableData"
            style="width: 100%"
            max-height="340">
            <el-table-column type="expand">
              <template slot-scope="props">
                <el-form label-position="left" inline class="demo-table-expand">
                  <el-form-item label="">
                    <span>{{ props.row.name }}</span>
                  </el-form-item>
                  <el-form-item label=" ID">
                    <span>{{ props.row.id }}</span>
                  </el-form-item>
                  <el-form-item label="email">
                    <span>{{ props.row.email }}</span>
                  </el-form-item>
                </el-form>
              </template>
            </el-table-column>
            <el-table-column
              label=""
              prop="name"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              label="email"
              prop="email"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              type="selection"
              width="55">
            </el-table-column>
          </el-table>
          <div class="hr-intvwer-footer">
            <el-button size="medium" type="danger" plain @click="hrDialogFormVisible = true"> </el-button>
            <el-button size="medium" type="primary" plain @click="addIntvwerDialogFormVisible = true"> </el-button>
          </div>
        </el-col>
      </el-row>
    </el-dialog>

    <!--  HR  -->
    <el-dialog title="" :visible.sync="addIntvweeDialogFormVisible">
      <el-table
        ref="multipleTable"
        :data="intvweeTableData"
        style="width: 100%"
        max-height="350">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="">
                <span>{{ props.row.name }}</span>
              </el-form-item>
              <el-form-item label=" ID">
                <span>{{ props.row.id }}</span>
              </el-form-item>
              <el-form-item label="email">
                <span>{{ props.row.email }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="name"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          label="email"
          prop="email">
        </el-table-column>
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
      </el-table>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addIntvweeDialogFormVisible = false"></el-button>
        <el-button type="primary" @click="addIntvweeDialogFormVisible = false"></el-button>
      </div>
    </el-dialog>

    <!--  HR  -->
    <el-dialog title="" :visible.sync="addIntvwerDialogFormVisible">
      <el-table
        ref="multipleTable"
        :data="intvwerTableData"
        style="width: 100%"
        max-height="350">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="">
                <span>{{ props.row.name }}</span>
              </el-form-item>
              <el-form-item label=" ID">
                <span>{{ props.row.id }}</span>
              </el-form-item>
              <el-form-item label="email">
                <span>{{ props.row.email }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="name"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          label="email"
          prop="email">
        </el-table-column>
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
      </el-table>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addIntvwerDialogFormVisible = false"></el-button>
        <el-button type="primary" @click="addIntvwerDialogFormVisible = false"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'AppMain',
  data: function () {
    return {
      editHrInfo: false,
      addIntvweeDialogFormVisible: false,
      addIntvwerDialogFormVisible: false,
      intvweeNum: 11, //  API  interviewee 
      intvwDialogFormVisible: false, // 
      hrDialogFormVisible: true, // 
      form: { // 
        name: 'default',
        email: 'default@default.com',
        region: 'default'
      },
      formLabelWidth: '120px', // 
      hrFormLabelWidth: '70px', // 
      checked: false, // 
      isDeleting: false, // 
      intvweeTableData: [{
        id: '1',
        name: '1',
        email: 'aaa@gmail.com',
        category: ''
      }, {
        id: '2',
        name: '2',
        email: 'bbb@gmail.com',
        category: ''
      }, {
        id: '3',
        name: '3',
        email: 'ccc@gmail.com',
        category: ''
      }, {
        id: '4',
        name: '4',
        email: 'ddd@gmail.com',
        category: ''
      }],
      intvwerTableData: [{
        id: '1',
        name: '1',
        email: 'aaa@gmail.com',
        category: ''
      }, {
        id: '2',
        name: '2',
        email: 'bbb@gmail.com',
        category: ''
      }]
    }
  },
  computed: {
    rowNum: function () { // 
      return Math.floor(this.intvweeNum / 3)
    },
    lastRow: function () { // 
      return this.intvweeNum - 3 * this.rowNum
    }
  },
  methods: {
    clickCard: function () {
      this.intvwDialogFormVisible = true
    },
    handleDelete: function () {
      //  RightMenu 
      // note:  delete 
      this.isDeleting = true
    },
    clickCancelDelete: function () {
      this.isDeleting = false
    },
    clickConfirmDelete: function () {
      this.isDeleting = false
    }
  },
  created: function () {
    this.$eventHub.$on('click-delete', this.handleDelete)
  },
  beforeDestory: function () {
    this.$eventHub.$off('click-delete')
  }
}
</script>

<style scoped>
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both
}

.box-card {
  width: 400px;
  height: 200px;
  margin-left: 40px;
  border-radius: 20px;
  border-width: 2px;
}

.el-row {
  margin-bottom: 20px;
}
.el-col {
  border-radius: 4px;
  height: 100%;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}

.el-input {
  width: 90%;
}

.el-form {
  text-align: left;
}

.el-checkbox {
  float: right;
}

.cancel-delete {
  position: fixed;
  right: 150px;
  bottom: 25px;
}

.confirm-delete {
  position: fixed;
  right: 40px;
  bottom: 25px;
}

.hrinfo-footer,
.hr-intvwee-footer,
.hr-intvwer-footer {
  float: right;
  padding-top: 20px;
}
</style>



